import * as React from 'react';
import { ProgressBar } from 'react-bootstrap';

const Progress = ({ value, onLapse }) => {
    const [counter, setCounter] = React.useState(value * 100);

    React.useEffect(() => {
        (counter > 0)  && 
            setTimeout(() => {
                setCounter(counter - 10);
            }, 100);
            (counter <= 0)  && setTimeout(onLapse, 200);
    });

    return <ProgressBar variant="primary" min={0} max={value * 100} now={counter} animated />;
}

export default Progress;