import * as React from 'react';

import ReactDataGrid from 'react-data-grid';
import { fetchLeadershipBoardData } from '../actions/dataService';
import { GameReducerType_ReactionTime, GameReducerType_VerbalMemory, GameReducerType_VisualMemory, GameReducerType_FraudDetection, GameReducerType_NumberMemory } from '../constants';

const columns = [{key: 'rank', name: 'Rank#'}, { key: 'name', name: 'Name' }, { key: 'email', name: 'Email ID' }, {key: 'mobile', name: 'Mobile No'},  {key:'score',  name:'Score'}];
const gameTypeMap = {
    [GameReducerType_NumberMemory]: 'Number Memory Game',
    [GameReducerType_ReactionTime]: 'Reaction Time Game',
    [GameReducerType_VerbalMemory]: 'Verbal Memory Game',
    [GameReducerType_VisualMemory]: 'Visual Memory Game',
    [GameReducerType_FraudDetection]: 'Fraud Detection Game'
}

const LeadershipBoard = ({ game }) => {
    const [data, setData] = React.useState([]);
    const rowGetter = rowNumber => data[rowNumber];

    React.useEffect(() => {
        game && fetchLeadershipBoardData(game).then(data => {
            setData(data);
        });
    }, [game]);

    console.log(game);
    console.log(gameTypeMap)

    return (
        <div className="flex flex1 flex-col">
            <div className="ResultText mar-20">Leadership Board for {gameTypeMap[game]}</div>
            <ReactDataGrid
                columns={columns}
                rowGetter={rowGetter}
                rowsCount={data.length}
                minHeight={500} />
        </div>
    );
};

export default LeadershipBoard;