import * as React from 'react';
import { Button } from 'react-bootstrap';
import Progress from './ProgressBar';
import { connect } from 'react-redux';
import { UserReducerType_SignOut, StatusReducerType_Posting, StatusReducerType_Failed, StatusReducerType_Idle, StatusReducerType_Success } from '../constants';
import { submitScore } from '../actions';
import LeadershipBoard from './LeadershipBoard';
import './NumberMemory.css'

const NumberMemory = () => {
    const [screen, setScreen] = React.useState('Splash');

    return <div className="GameScreen1 flex flex1 flex-col">
        <div className="flex flex1 justify-center bg-orange">
            {screen === 'Splash' && <SplashScreen setScreen={setScreen} />}
            {screen === 'Game' && <GameScreen />}
        </div>
    </div>
};

export default NumberMemory;

const SplashScreen = ({ setScreen }) => {
    return (
        <div className="align-center flex flex-col flex1 justify-center">
            <div className="GameTitle flex justify-center mar-30">Number Memory Game</div>
            <div className="ScreenText flex flex-col justify-center">
                <div className="flex justify-center TextTitle">Can you Memorize all the Numbers?</div>
                <div className="flex justify-center Text">On an average a person can remember upto 7 digit number. Can you do more?</div>
            </div>
            <div className="flex align-center justify-center">
                <Button variant="success" onClick={() => setScreen('Game')}>Start</Button>
            </div>
        </div>
    )
};

const generateRandomNumber = (digits) => {
    const result = [];
    for (let i = 0; i < digits; i++) {
        result.push(Math.floor(Math.random() * Math.floor(10)))
    }

    return result.join('');
};

const GameScreen = ({ setScore }) => {
    const [level, setLevel] = React.useState(1);
    const [screen, setScreen] = React.useState('Display');
    const numberValue = React.useRef(generateRandomNumber(level));

    const handleChangeLevel = () => {
        numberValue.current = generateRandomNumber(level + 1);
        setLevel(level + 1);
        setScreen('Display');
    };

    if (screen === 'Display') {
        return <DisplayScreen value={numberValue.current} level={level} showInput={() => setScreen('Input')} />;
    } else if (screen === 'Input') {
        return <InputScreen value={numberValue.current} level={level} nextLevel={handleChangeLevel} submitScore={setScore} />;
    }
};

const DisplayScreen = ({ value, level, showInput }) => {
    return (
        <div className="GameScreen justify-center">
            <div className="NumberText flex justify-center">{value}</div>
            <div className=""><Progress value={level} onLapse={showInput} /></div>
        </div>
    );
};

const computeDifference = (displayValue, inputValue) => {
    const len = displayValue.length > inputValue.length ? displayValue.length : inputValue.length;
    const result = [];

    for (let i = 0; i < len; i++) {
        let notMatch = false;
        if (displayValue.substr(i, 1) !== inputValue.substr(i, 1)) {
            notMatch = true;
        }

        result.push(<div key={i} className={`${notMatch ? 'noMatch' : ''}`}>{inputValue.substr(i, 1)}</div>)
    }

    return result;
};

const InputScreen_Internal = ({ value, level, nextLevel, status, submitGameScore, restartGame,  game }) => {
    const [screen, setScreen] = React.useState('Input');
    const [inputValue, setInputValue] = React.useState('');
    const prevState = React.useRef(StatusReducerType_Idle);
    const retries = React.useRef(0);

    const _input = React.useRef(null);

    React.useEffect(() => {
        _input.current && _input.current.focus();

        if (prevState.current !== status) {
            prevState.current = status;

            if (status === StatusReducerType_Success) {
                setTimeout(() => setScreen('leaderboard'), 3000);
            } else if (status === StatusReducerType_Failed) {
                if (retries.current < 3) {
                    setTimeout(() => { submitGameScore(level - 1) }, 3000);
                    retries.current++;
                } else {
                    restartGame();
                }
            }
        }

        if (screen === 'leaderboard'){
            setTimeout(restartGame,  10000);
        }
    }, [screen, status, _input, level, submitGameScore, restartGame]);

    const handleSubmit = () => {
        if (value !== inputValue.trim()) {
            submitGameScore(level - 1);
        }

        setScreen('Review');
    }

    const handleChange = ({ target }) => {
        setInputValue(target.value);
    };

    const handleKeyPress = ({ key }) => {
        if (key === 'Enter') {
            setScreen('Review');
        }
    };

    if (screen === 'Input') {
        return (
            <div className="GameInputScreen bg-orange">
                <div className="InputForm flex flex-col justify-center align-center">
                    <div className="FormTitle flex justify-center flex-col">
                        <h2 className="flex align-center justify-center">What was the number?</h2>
                        <div className="hintText flex align-center justify-center">Press Enter to Submit</div>
                    </div>
                    <input type="text" pattern="[0-9]*" className="GameInputText white" align="center" ref={_input} onChange={handleChange} onKeyPress={handleKeyPress} autoFocus />
                    <div className="FormInput">
                        <Button variant="info" onClick={handleSubmit}>Submit</Button>
                    </div>
                </div>
            </div>
        );
    } else if (screen === 'Review') {
        return (
            <div className="ReviewScreen flex flex-col">
                <div className="ResultGroup flex flex-col">
                    <div className="ResultTitle flex justify-center">Number Displayed</div>
                    <div className="ResultText flex justify-center">{value}</div>
                </div>
                <div className="ResultGroup flex flex-col">
                    <div className="ResultTitle flex justify-center">Your Answer</div>
                    <div className={`ResultText flex justify-center`}>{value === inputValue.trim() ? inputValue : computeDifference(value, inputValue)}</div>
                </div>
                <div className="LevelText flex justify-center">Level {level}</div>
                <div className="ResultAction flex justify-center">
                    {value === inputValue.trim() ?
                        <Button variant="primary" onClick={nextLevel}>Next</Button> :
                        (
                            status === StatusReducerType_Posting ? <div className="StatusText">Submitting Score...</div> :
                                status === StatusReducerType_Failed ? <div className="StatusText failed">Score submit failed... retrying...</div> : <div className="StatusText success">Score submitted successfully.</div>)}
                </div>
            </div>
        );
    } else  if (screen === 'leaderboard') {
        return <LeadershipBoard game={game} />;
    }
};

const mapStateToProps = ({ status, game }) => ({ status, game });

const mapDispatchToProps = dispatch => {
    return {
        restartGame: () => dispatch({ type: UserReducerType_SignOut }),
        submitGameScore: (level) => dispatch(submitScore(level))
    };
};

const InputScreen = connect(mapStateToProps, mapDispatchToProps)(InputScreen_Internal);