import * as React from 'react';
import {connect} from 'react-redux';
import LeadershipBoard  from './LeadershipBoard';
import './ReactionTime.css';
import { submitScore } from '../actions';
import { StatusReducerType_Idle, StatusReducerType_Posting, StatusReducerType_Success, StatusReducerType_Failed, UserReducerType_SignOut } from '../constants';

const generateRandomNumber = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

const ReactionTime = ({status, game, submitGameScore, restartGame}) => {
    const [screen, setScreen] = React.useState('Splash');

    const startTime = React.useRef(null);
    const endTime = React.useRef(null);
    const tries = React.useRef(0);
    const totalTimeTaken = React.useRef(0);
    const timeoutHandle = React.useRef(null);
    const prevState = React.useRef(StatusReducerType_Idle);
    const retries = React.useRef(0);

    const handleClick = () => {
        if (timeoutHandle.current) {
            clearTimeout(timeoutHandle.current);
            timeoutHandle.current = null;
        }
        if (tries.current === 5) {
            return;
        }
        if (screen === 'Splash' || screen === 'Oops') {
            setScreen('Wait');
        } else if (screen === 'Wait') {
            setScreen('Oops');
        } else if (screen === 'Click') {
            endTime.current = Date.now();
            tries.current++;
            totalTimeTaken.current += (endTime.current - startTime.current);
            setScreen('Review');
        } else if (screen === 'Review') {
            startTime.current = null;
            endTime.current = null;
            setScreen('Wait');
        }
    };

    React.useEffect(() => {
        if (screen === 'Wait' && tries.current < 5) {
            timeoutHandle.current = setTimeout(() => {
                startTime.current = Date.now();
                setScreen('Click');
            }, generateRandomNumber(4, 9) * 1000)
        } else if (screen === 'Review' && tries.current === 5 && status === StatusReducerType_Idle) {
            submitGameScore(Math.round(totalTimeTaken.current / tries.current, 0))
        }

        if (prevState.current !== status) {
            prevState.current = status;

            if (status === StatusReducerType_Success) {
                setTimeout(() => setScreen('leaderboard'), 3000);
            } else if (status === StatusReducerType_Failed) {
                if (retries.current < 3) {
                    setTimeout(() => { submitGameScore(Math.round(totalTimeTaken.current / tries.current, 0)) }, 3000);
                    retries.current++;
                } else {
                    restartGame();
                }
            }
        }

        if (screen === 'leaderboard') {
            setTimeout(restartGame, 10000);
        }
    }, [screen, status, restartGame, submitGameScore])

    const bgcolor = screen === 'Splash' || screen === 'Oops' || screen === 'Review' || screen === 'leaderboard' ? 'bg-orange' :
        screen === 'Wait' ? 'bg-red' :
            screen === 'Click' ? 'bg-green' : '';

    return (
        <div className={`flex flex1 justify-center cur-pointer user-select-none ${bgcolor} ${bgcolor !=='bg-orange' ? 'white' : ''}`} onClick={handleClick}>
            <div className="flex flex-col flex1 align-center justify-center">
                <div className="ScreenText InfoTextArea flex flex-col justify-center">
                    {screen === 'Splash' && <>
                        <div className="TextTitle flex justify-center">Reaction Time Game</div>
                        <div className="flex justify-center GameTitle font-sz-24">When the background color turns green, click as quickly as you can.</div>
                        <div className="flex justify-center Text">Click anywhere to start.</div>
                    </>}
                    {screen === 'Oops' && <>
                        <div className="flex justify-center TextTitle">Clicked too early. Wait for the color to change.</div>
                        <div className="flex justify-center Text">Click to try again.</div>
                    </>}
                    {screen === 'Wait' && <>
                        <div className="flex justify-center TextTitle">Wait for green</div>
                    </>}
                    {screen === 'Click' && <>
                        <div className="flex justify-center TextTitle">Click!</div>
                    </>}
                    {screen === 'Review' && <>
                        <div className="flex justify-center TextTitle">{endTime.current - startTime.current} ms</div>
                        {tries.current < 5 ?
                            <div className="flex justify-center Text">Click to keep going.</div> :
                            (
                                status === StatusReducerType_Posting ? <div className="StatusText">Submitting Score...</div> :
                                    status === StatusReducerType_Failed ? <div className="StatusText failed">Score submit failed... retrying...</div> : <div className="StatusText success">Score submitted successfully.</div>)
                        }
                    </>}
                    {screen === 'leaderboard' && <LeadershipBoard game={game} />}
                </div>
                <div className="flex align-center justify-center">
                    <div>Average | {tries.current ? Math.round(totalTimeTaken.current / tries.current, 0) : 0}ms</div><div className="mar-left-30">Tries | {tries.current} of 5</div>
                </div>
            </div>
        </div >
    );
};

const mapStateToProps = ({ status, game }) => ({ status, game });

const mapDispatchToProps = dispatch => {
    return {
        restartGame: () => dispatch({ type: UserReducerType_SignOut }),
        submitGameScore: (avgTime) => dispatch(submitScore(avgTime))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ReactionTime);