import * as React from 'react';
import { connect } from 'react-redux';
import { Button } from 'react-bootstrap';
import { StatusReducerType_Posting, StatusReducerType_Failed, StatusReducerType_Idle, StatusReducerType_Success, UserReducerType_SignOut } from '../constants';
import { submitScore } from '../actions';
import LeadershipBoard from './LeadershipBoard';

const wordsList = ['Socure', 'Identity', 'Verification', 'Fraud Detection', 'Safety', 'Reliability', 'Customers', 'Client Trust', 'Artificial Intelligence', 'Machine Learning',
    'High Accuracy', 'Low Risk', 'Faceless', 'Business', 'Challenge', 'Credit Bureau', 'User Experience', 'Auto Approvals', 'Cut Fraud', 'Business Growth',
    'Financial', 'Institutions', 'Digital', 'Payments', 'Transactions', 'Fraudulent Identity', 'Services', 'Credits', 'Debits', 'Banks', 'Social Network',
    'Review', 'Technology', 'Approvals', 'Denial', 'Currency', 'Streamline', 'Revenue', 'Maintenance', 'Solution', 'Application', 'Identity Crisis', 'Aida',
    'Diversity', 'Personal', 'Identification', 'Gateway', 'Leadership', 'Trusted', 'Differentiator', 'Competitive', 'Data Science', 'Engineering',
    'Predictive Analysis', 'Analytics', 'Security', 'Social', 'Secure', 'Smarter', 'Brilliant', 'Deployment', 'Compliance', 'Brainstorming', 'Score',
    'Privacy', 'Management', 'Information', 'Genuine', 'Empower', 'Data Security', 'Big Data', 'Information Science', 'Data Scientist', 'Trust Builder', 'Fraud Fighter'];

const generateRandomNumber = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
}

const VerbalMemory = ({ status, game, submitGameScore, restartGame }) => {
    const [screen, setScreen] = React.useState('Splash');
    const [lives, setLives] = React.useState(3);
    const score = React.useRef(0);
    const words = React.useRef(10);
    const selectedIndex = React.useRef(generateRandomNumber(0, words.current));
    const selectedWords = React.useRef([]);
    const prevState = React.useRef(StatusReducerType_Idle);
    const retries = React.useRef(0);

    const [word, setWord] = React.useState(wordsList[selectedIndex.current]);

    React.useEffect(() => {
        if (lives === 0 && status === StatusReducerType_Idle) {
            submitGameScore(score.current);
        }

        if (prevState.current !== status) {
            prevState.current = status;

            if (status === StatusReducerType_Success) {
                setTimeout(() => setScreen('leaderboard'), 3000);
            } else if (status === StatusReducerType_Failed) {
                if (retries.current < 3) {
                    setTimeout(() => { submitGameScore(score.current) }, 3000);
                    retries.current++;
                } else {
                    restartGame();
                }
            }
        }

        if (screen === 'leaderboard') {
            setTimeout(restartGame, 10000);
        }
    }, [lives, status, screen, submitGameScore, restartGame]);

    const validateSelection = (type) => {
        const newWord = (selectedWords.current.indexOf(selectedIndex.current) === -1);
        newWord && selectedWords.current.push(selectedIndex.current);

        if ((newWord && type === 'SEEN') || (!newWord && type === 'NEW')) {
            setLives(lives - 1);
        } else {
            score.current++;
        }

        if (score.current > 50) {
            words.current = wordsList.length;
        } else if (score.current + 5 > words.current) {
            words.current += 10;
        }

        if (lives > 0) {
            let randInd = generateRandomNumber(0, words.current);
            while (randInd === selectedIndex.current) {
                randInd = generateRandomNumber(0, words.current);
            }
            selectedIndex.current = randInd;
            setWord(wordsList[selectedIndex.current]);
        }
    };

    return (
        <div className="flex flex1 justify-center bg-orange">
            <div className="flex flex1 align-center justify-center">
                <div className="ScreenText InfoTextArea flex flex-col justify-center">
                    {screen === 'Splash' && <>
                        <div className="TextTitle flex justify-center">Verbal Memory Game</div>
                        <div className="flex justify-center GameTitle font-sz-20">You will be shown words, one at a time.</div>
                        <div className="flex justify-center GameTitle font-sz-20">If you have seen a word during the game, click SEEN</div>
                        <div className="flex justify-center GameTitle font-sz-20">If it is new word, click NEW</div>
                        <div className="flex align-center justify-center mar-top-30">
                            <Button variant="success" onClick={() => setScreen('Game')}>Start</Button>
                        </div>
                    </>}
                    {screen === 'Game' && <>
                        <div className="flex align-center justify-center font-sz-20">
                            <div>Lives | {lives}</div><div className="mar-left-30">Score | {score.current}</div>
                        </div>
                        <div className="flex justify-center TextTitle mar-top-30">{word}</div>
                        {lives > 0 ?
                            <div className="flex justify-center">
                                <Button variant="success" className="button1" onClick={() => validateSelection('SEEN')}>SEEN</Button>
                                <Button variant="success" className="button1 mar-left-60" onClick={() => validateSelection('NEW')}>NEW</Button>
                            </div> :
                            (
                                status === StatusReducerType_Posting ? <div className="StatusText">Submitting Score...</div> :
                                    status === StatusReducerType_Failed ? <div className="StatusText failed">Score submit failed... retrying...</div> : <div className="StatusText success">Score submitted successfully.</div>)
                        }
                    </>}
                    {screen === 'leaderboard' && <LeadershipBoard game={game} />}
                </div>
            </div>
        </div >
    );
};

const mapStateToProps = ({ status,  game }) => ({ status, game });

const mapDispatchToProps = dispatch => {
    return {
        restartGame: () => dispatch({ type: UserReducerType_SignOut }),
        submitGameScore: (score) => dispatch(submitScore(score))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(VerbalMemory);