import * as React from 'react';
import { FraudData } from '../data/fraud';
import { Button, Card } from 'react-bootstrap';
import { StatusReducerType_Posting, StatusReducerType_Failed, StatusReducerType_Success, UserReducerType_SignOut, StatusReducerType_Idle } from '../constants';
import './FraudDetection.css';
import { submitScore } from '../actions';
import { connect } from 'react-redux';
import Countdown from 'react-countdown';
import LeadershipBoard from './LeadershipBoard';

const getProfiles = () => {
    const profiles = [];
    let fraudDataFound = false;

    while (profiles.length < 3) {
        const randNo = Math.floor(Math.random() * Math.floor(FraudData.length));
        if (profiles.indexOf(randNo) === -1) {
            if (FraudData[randNo].FraudLabel === 'Fraud' && fraudDataFound === false) {
                profiles.push(randNo);
                fraudDataFound = true;
            } else {
                if (fraudDataFound === false) {
                    profiles.length < 2 && profiles.push(randNo);
                } else {
                    profiles.push(randNo);
                }
            }
        }
    }

    return profiles.map(id => ({ ...FraudData[id], id }));
};

const FraudDetection = ({ status, game, submitGameScore, restartGame }) => {
    const [screen, setScreen] = React.useState('Splash');
    const isAnswerCorrect = React.useRef(false);
    const answer = React.useRef(null);
    const prevState = React.useRef(StatusReducerType_Idle);
    const retries = React.useRef(0);
    const timeOut = React.useRef(false);
    const startTime = React.useRef(null);

    const  handleStartGame = () => {
        setScreen('Game');
        startTime.current = Date.now();
    };

    const handleTimeout = () => {
        timeOut.current = true;
        submitGameScore(0);
        setScreen('GameOver');
    };

    const handleDecision = (id) => {
        answer.current = id;
        isAnswerCorrect.current = FraudData[id].FraudLabel === 'Fraud';

        submitGameScore(isAnswerCorrect.current ? Math.round((Date.now() - startTime.current)/1000, 0): 0);

        setScreen('GameOver');
    };

    React.useEffect(() => {
        if (prevState.current !== status) {
            prevState.current = status;

            if (status === StatusReducerType_Success) {
                setTimeout(() => {setScreen('leaderboard');}, 3000);
            } else if (status === StatusReducerType_Failed) {
                if (retries.current < 3) {
                    setTimeout(() => { submitGameScore(isAnswerCorrect.current ? 1 : 0) }, 3000);
                    retries.current++;
                } else {
                    restartGame();
                }
            }
        }

        if (screen === 'leaderboard') {
            setTimeout(restartGame, 10000);
        }
    }, [screen, status, submitGameScore, restartGame])

    return (
        <div className="flex flex1 justify-center bg-orange visual-test">
            <div className="flex flex1 align-center justify-center">
                <div className="ScreenText InfoTextArea flex flex-col justify-center relative">
                    {screen === 'Splash' && <>
                        <div className="TextTitle flex justify-center">Fraud Detection</div>
                        <div className="flex justify-center GameTitle font-sz-20">3 known celebrities data will be presented in a respective card.</div>
                        <div className="flex justify-center GameTitle font-sz-20">The details about every celebrity will be displayed in the card.</div>
                        <div className="flex justify-center GameTitle font-sz-20">As a detective, you have to identify the character who has FRAUDULENT data.</div>
                        <div className="flex justify-center GameTitle font-sz-20">You have 90 seconds to decide.</div>
                        <div className="flex align-center justify-center mar-top-30">
                            <Button variant="success" onClick={handleStartGame}>Start</Button>
                        </div>
                    </>}
                    {(screen === 'Game' || screen === 'Review') && <>
                        <div className="flex align-center justify-center">
                            <Game data={getProfiles()} onAnswer={handleDecision} onTimeout={handleTimeout} />
                        </div>
                    </>}
                    {(screen === 'GameOver') && <>
                        <div className="flex flex1 align-center justify-center">
                            <div className="flex flex-col">
                                <div className="LevelText flex justify-center">
                                    {timeOut.current && <div>Sorry your time is up!!!.</div>}
                                    {timeOut.current === false && (isAnswerCorrect.current ? <div className="">Wow!!! You nailed it.</div> : <div className="">Sorry!!! It is not the right answer. You have chosen {FraudData[answer.current].Name}'s profile.</div>)}
                                </div>
                                <div className="ResultAction flex justify-center">
                                    {status === StatusReducerType_Posting ? <div className="StatusText">Submitting Score...</div> :
                                        status === StatusReducerType_Failed ? <div className="StatusText failed">Score submit failed... retrying...</div> : <div className="StatusText success">Score submitted successfully.</div>}
                                </div>
                            </div>
                        </div>
                    </>}
                    {screen === 'leaderboard' && <LeadershipBoard game={game} />}
                </div>
            </div>
        </div >
    );
};

const Game = ({ data, onAnswer, onTimeout }) => {
    const [selectedCard, setSelectedCard] = React.useState(null);
    const [answer, setAnswer] = React.useState('');

    const prefixZero = (value) => {
        return value < 10 ? `0${value}` : value;
    };

    const handleCardClick = (id, character) => {
        setSelectedCard({ ...FraudData[id], id, character });
    }

    const handleCardClose = () => {
        setSelectedCard(null);
    }

    const checkAnswer = () => {
        onAnswer(parseInt(answer, 10));
    }

    const renderer = ({ hours, minutes, seconds, completed }) => {
        if (completed) {
          onTimeout();
          return null;
        } else {
          return <span>{prefixZero(hours)}:{prefixZero(minutes)}:{prefixZero(seconds)}</span>;
        }
      };

    if (selectedCard) {
        return (<GameCard {...selectedCard} onClose={handleCardClose} onAnswer={onAnswer} selected />)
    } else {
        return (
            <div className="fraud-detection-game flex flex1 flex-col">
                <div className="count-down absolute">
                    <Countdown
                        date={Date.now() + 90000}
                        renderer={renderer}
                    />
                </div>
                <div className="char-cards flex flex1 overflow-hidden">
                    {data.map((d, idx) => <GameCard key={d.Name} {...d} onClick={handleCardClick} onAnswer={onAnswer} selected={false} character={idx + 1} />)}
                </div>
                <div className="flex flex1 align-center justify-center mar-top-30">
                    <div className="flex align-center justify-center">
                        <div>The celebrity with fraudulent data is</div>
                        <select className="mar-left-10" selected={answer} onChange={({ target }) => { setAnswer(target.value) }}>
                            <option value="">--select--</option>
                            {data.map((d, idx) => <option key={d.id} value={d.id}>Character {idx + 1}</option>)}
                        </select>
                        <Button className="mini-btn mar-left-10" variant="success" onClick={checkAnswer} disabled={!!!answer}>Submit</Button>
                    </div>
                </div>
            </div>
        );
    }
};

