import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import { UserReducerType_SignIn, UserReducerType_SignOut, GameReducerType_NumberMemory, GameReducerType_ReactionTime, GameReducerType_VerbalMemory, GameReducerType_VisualMemory, StatusReducerType_Idle, StatusReducerType_Failed, StatusReducerType_Posting, StatusReducerType_Success, GameReducerType_FraudDetection, GameReducerType_Locked } from '../constants';

const userReducer = (currentState = null, action) => {
    switch (action.type) {
        case UserReducerType_SignIn:
            return action.data;
        case UserReducerType_SignOut:
            return null;
        default:
            return currentState;
    }
};

const gameReducer = (currentState = GameReducerType_Locked, action) => {
    switch (action.type) {
        case GameReducerType_NumberMemory:
        case GameReducerType_ReactionTime:
        case GameReducerType_VerbalMemory:
        case GameReducerType_VisualMemory:
        case GameReducerType_FraudDetection:
        case GameReducerType_Locked:
            localStorage.setItem('gameType', action.type);
            return action.type;
        default:
            return currentState;
    }
}

const statusReducer = (currentState = StatusReducerType_Idle, action) => {
    switch (action.type) {
        case StatusReducerType_Idle:
        case StatusReducerType_Posting:
        case StatusReducerType_Success:
        case StatusReducerType_Failed:
            return action.type;
        default:
            return currentState;
    }
};

const gameType = localStorage.getItem('gameType') || GameReducerType_NumberMemory;

const store = createStore(combineReducers({
    user: userReducer,
    game: gameReducer,
    status: statusReducer,
}), { game: gameType }, applyMiddleware(thunk));

export default store;