import { postScore } from './dataService';
import { StatusReducerType_Posting, StatusReducerType_Success, StatusReducerType_Failed } from '../constants';

export const submitScore = (level) => (dispatch, getState) => {
    const { user, game } = getState();

    dispatch({ type: StatusReducerType_Posting });

    postScore(user, game, level)
        .then(() => {
            dispatch({ type: StatusReducerType_Success });
        })
        .catch((error) => {
            console.log("Failed: ", error);
            dispatch({ type: StatusReducerType_Failed });
        });
};