import * as React from 'react';
import {connect} from 'react-redux';
import { Button } from 'react-bootstrap';
import './VisualMemory.css';
import { StatusReducerType_Posting, StatusReducerType_Failed, StatusReducerType_Idle, StatusReducerType_Success, UserReducerType_SignOut } from '../constants';
import { submitScore } from '../actions';
import LeadershipBoard from './LeadershipBoard';

const buildPattern = (tilesCount, activeTilesCount) => {
    let matrix = [];
    let activeTiles = 0;

    for (let i = 0; i < tilesCount; i++) {
        matrix[i] = [];
        for (let j = 0; j < tilesCount; j++) {
            let value = activeTiles < activeTilesCount ? Math.floor(Math.random() * Math.floor(2)) : 0;
            if (value) {
                activeTiles++;
            }
            matrix[i][j] = value;
        }
    }

    if (activeTiles < activeTilesCount) {
        while (activeTiles < activeTilesCount) {
            const x = Math.floor(Math.random() * Math.floor(tilesCount));
            const y = Math.floor(Math.random() * Math.floor(tilesCount));

            if (matrix[x][y] === 0) {
                matrix[x][y] = 1;
                activeTiles++;
            }
        }
    }

    return matrix;
};

const tilesMap = {
    1: 3,
    3: 4,
    6: 5,
    9: 6,
    14: 7,
    19: 8
}

const VisualMemory = ({ status, game, submitGameScore, restartGame }) => {
    const [screen, setScreen] = React.useState('Splash');
    const lives = React.useRef(3);
    const [pattern, setPattern] = React.useState(buildPattern(3, 3));
    const level = React.useRef(1);
    const tiles = React.useRef(3);
    const prevState = React.useRef(StatusReducerType_Idle);
    const retries = React.useRef(0);

    const addScore = () => {
        level.current++;
        setScreen('Review');
        if (tilesMap[level.current]) {
            tiles.current = tilesMap[level.current];
        }
        setPattern(buildPattern(tiles.current, level.current + 2));
    };

    const setLifeLost = () => {
        lives.current--;

        if (lives.current > 0) {
            setScreen('Review');
            setPattern(buildPattern(tiles.current, level.current + 2));
        } else {
            setScreen('GameOver');
            submitGameScore(level.current - 1);
        }
    };

    React.useEffect(() => {
        if (screen === 'Review' && lives.current > 0) {
            setTimeout(() => setScreen('Game'), 1000);
        }

        if (prevState.current !== status) {
            prevState.current = status;

            if (status === StatusReducerType_Success) {
                setTimeout(() => setScreen('leaderboard'), 3000);
            } else if (status === StatusReducerType_Failed) {
                if (retries.current < 3) {
                    setTimeout(() => { submitGameScore(level.current - 1) }, 3000);
                    retries.current++;
                } else {
                    restartGame();
                }
            }
        }

        if (screen === 'leaderboard') {
            setTimeout(restartGame, 1000);
        }
    }, [screen, status, submitGameScore, restartGame])

    return (
        <div className="flex flex1 justify-center bg-orange visual-test">
            <div className="flex flex1 align-center justify-center">
                <div className="ScreenText InfoTextArea flex flex-col justify-center">
                    {screen === 'Splash' && <>
                        <div className="TextTitle flex justify-center">Visual Memory Game</div>
                        <div className="flex justify-center GameTitle font-sz-20">Every level, a number of tiles will appear with Socure logo. Memorize them, and pick them again after the tiles are reset!</div>
                        <div className="flex justify-center GameTitle font-sz-20">Levels get progressively more difficult, to challenge your skills.</div>
                        <div className="flex justify-center GameTitle font-sz-20">If you miss 3 tiles on a level, you lose one life.</div>
                        <div className="flex justify-center GameTitle font-sz-20">You have three lives.</div>
                        <div className="flex justify-center GameTitle font-sz-20">Make it as far as you can!</div>
                        <div className="flex align-center justify-center mar-top-30">
                            <Button variant="success" onClick={() => setScreen('Game')}>Start</Button>
                        </div>
                    </>}
                    {(screen === 'Game' || screen === 'Review') && <>
                        <div className="flex align-center justify-center">
                            <div className="score"><span className="faint-label">Level: </span> {level.current}</div><div className="score mar-left-30"><span className="faint-label">Lives: </span> {lives.current}</div>
                        </div>
                        {screen === 'Game' ?
                            <Matrix tiles={tiles.current} pattern={pattern} setLifeLost={setLifeLost} lives={lives.current} addScore={addScore} /> :
                            <EmptyMatrix tiles={tiles.current} pattern={buildPattern(tiles.current, 0)} />
                        }
                    </>}
                    {(screen === 'GameOver') && <>
                        <div className="flex flex1 align-center justify-center">
                            <div className="flex flex-col">
                                <div className="LevelText flex justify-center">You have passed {level.current - 1} Levels.</div>
                                <div className="ResultAction flex justify-center">
                                    {status === StatusReducerType_Posting ? <div className="StatusText">Submitting Score...</div> :
                                        status === StatusReducerType_Failed ? <div className="StatusText failed">Score submit failed... retrying...</div> : <div className="StatusText success">Score submitted successfully.</div>}
                                </div>
                            </div>
                        </div>
                    </>}
                    {screen === 'leaderboard' && <LeadershipBoard game={game} />}
                </div>
            </div>
        </div >
    );
};

const isPatternMatched = (patterns, selected, tiles) => {
    let retVal = true;

    for (let x = 0; x < tiles; x++) {
        for (let y = 0; y < tiles; y++) {
            if (patterns[x][y] === 1 && !selected[`${x}${y}`]) {
                retVal = false;
            }
        }
    }

    return retVal;
};

const Matrix = ({ tiles, pattern, setLifeLost, addScore, lives }) => {
    const [view, setView] = React.useState('');
    const [selected, setSelected] = React.useState({});
    const chances = React.useRef(3);
    const prevLives = React.useRef(lives);

    const handleClick = (x, y) => {
        const match = pattern[x][y] === 1;
        const newSelected = { ...selected, [`${x}${y}`]: match };
        setSelected(newSelected);

        if (!match) {
            chances.current--;

            if (chances.current === 0) {
                chances.current = 3;
                prevLives.current = lives;
                setView('Review');
                setTimeout(() => {
                    setSelected({});
                    setLifeLost();
                }, 1000);
            }
        } else if (isPatternMatched(pattern, newSelected, tiles)) {
            chances.current = 3;
            prevLives.current = lives;
            setView('Review');
            setTimeout(addScore, 1000);
        }
    };

    React.useEffect(() => {
        if (lives === prevLives.current) {
            if (view === 'Preview') {
                setSelected({});
                setTimeout(() => setView('Game'), 2000);
            }

            view === '' && setTimeout(setView('Preview'), 2000);
        } else {
            if (lives > 0) {
                setTimeout(setView('Preview'), 2000);
            }
        }
    }, [view, lives]);

    const retVal = [];
    for (let x = 0; x < tiles; x++) {
        retVal.push(<MatrixRow key={x} x={x} tiles={tiles} pattern={pattern} view={view} selected={selected} onClick={handleClick} />)
    }
    return <div className="matrix">{retVal}</div>;
};

const EmptyMatrix = ({ tiles, pattern }) => {
    const retVal = [];
    for (let x = 0; x < tiles; x++) {
        retVal.push(<MatrixRow key={x} x={x} tiles={tiles} pattern={pattern} view={'Game'} selected={{}} onClick={() => { }} />);
    }
    return <div className="matrix">{retVal}</div>;
};

const MatrixRow = ({ x, tiles, pattern, view, selected, onClick }) => {
    const retVal = [];
    for (let y = 0; y < tiles; y++) {
        retVal.push(<Square key={`${x}${y}`} x={x} y={y} pattern={pattern} tiles={tiles} view={view} selected={selected} onClick={onClick} />)
    }

    return <div className="matrix-row">{retVal}</div>;
};

const Square = ({ x, y, pattern, tiles, view, selected, onClick }) => {
    let className = "";

    const handleClick = () => {
        onClick(x, y);
    };

    let value;

    if (view === "Preview" && pattern[x][y]) {
        className = "active";
    } else if (view === "Game" || view === "Review") {
        value = selected[`${x}${y}`];
        className = value === true ? 'active' : value === false ? 'active error' : 'cur-pointer';
    }

    return (
        <div className={`square s${tiles} ${className}`} {...(view === 'Game' && value === undefined && { onClick: handleClick })} />
    );
};

const mapStateToProps = ({ status, game }) => ({ status, game });

const mapDispatchToProps = dispatch => {
    return {
        restartGame: () => dispatch({ type: UserReducerType_SignOut }),
        submitGameScore: (level) => dispatch(submitScore(level))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(VisualMemory);